<template>
  <div class="hello">
      <form>
          <label from="url">Endpoint url</label>
          <input type="text" name="url" v-model="url" required>


          <input type="hidden" name="parentid" v-bind:value="parentid">
          <input type="button" value="Create endpoint" v-on:click="createEndpoint">
      </form>
  </div>
</template>

<script>
import Sitemap from "../store/sitemap";


export default {
  name: 'midnight-add',
  data () {
    return {
      url: ""
    }
  },
  props:['parentid'],
  methods:{
      createEndpoint: function(event){
          //validate form///

          console.log("creating endpoint")
          let end  = {
              url: this.url,
              parentid: this.$props.parentid,
              childRoutes:{},//vide par defaut
          };
          Sitemap.addNewEndpoint(end).then( (res)=>{
              console.log(res)
              //enregistre le nouveau sitemap???
              //recup le parentid et append en child routes
              

              //everything is ok, go back...
              console.log("return to sitemap")
              this.$router.push('/');
          }).catch( (err)=>{

          });

      }
  }
}
</script>

