<template>
    
    
    
        <li>
          
            <div class="endpoint" draggable="true" :id="item.__uuid__">
              
                <div>
                    <h3>{{url}}</h3>
                    <a v-on:click="deleteEndpoint(item)">Delete endpoint</a>
                </div>
                <!-- les methodes supportées -->
                
                <div v-for="(method, name) of http_methods">
                <!-- autres parametres de la vue (query)... -->
                      <span>{{name}}</span>
                      <div v-for="(prop, pn) in simple_props(method)">
                          <span class="param_name">{{pn}}: </span><span>{{prop}}</span>
                      </div>

                      <!-- parametres de requetes -->
                      <div v-if="method.params" class="parameters">
                          <span class="param_name">Parametres:</span>
                          
                              <div v-for="param in method.params" >
                                <span class="param_name">{{param.name}}</span>
                              <span>({{param.type}})</span>
                              </div>
                          
                      </div>
                      <div v-if="method.url_params" class="parameters">
                          <span class="param_name">URL Parametres:</span>
                          
                              <div v-for="qparam in method.url_params">
                                <span class="param_name">{{qparam.name}}</span>
                              <span>({{qparam.type}})</span>
                              </div>
                          
                      </div>
                </div>
            </div>
            <ul v-if="item.childRoutes">
                <map-item 
                v-for="(child, key) in item.childRoutes" 
                :key="child.__uuid__" 
                :item="child" :url="key"></map-item>
                <li>
                  <div class="drag-cible" data-drop="true" v-bind:id="item.__uuid__">
                      <router-link class="round-btn" :to="{path:'/addEndpoint/'+item.__uuid__}">
                  <i class="material-icons">add</i></router-link></div>
              </li>
            </ul>
            <ul v-else>
                <li>
                  <div class="drag-cible" data-drop="true" v-bind:id="item.__uuid__">
                      <router-link :to="{path:'/addEndpoint/'+item.__uuid__}" class="round-btn">
                  <i class="material-icons">add</i></router-link></div>
              </li>
            </ul>
        </li>

    
</template>
<script>

import Sitemap from '../store/sitemap'

export default {
  name: 'map-item',  
  props:["item","url"],
  computed:{
      http_methods: function(){
          let obj = {};
          for(let key in this.item){
              if(key!="childRoutes" && !key.startsWith("__")) obj[key]=this.item[key];
          }
          return obj;
      }
  },
  methods:{
      simple_props: function(who){
          let obj = {};
          for(let k in who){
              if(!Array.isArray(who[k])) obj[k]=who[k];
          }
          return obj;
      },
      deleteEndpoint : function(item){
          //supprime l'element
          Sitemap.deleteEndpoint(item.__uuid__).then( (res)=>{
              console.log("OK");
          }).catch( (err)=>{

          });
      }
  }
  
}

</script>
<style lang="scss">
@import "./scss/test";

</style>


