export default {
    datas: {
        title: 'Sitemap RENDERING',
        sitemap: null
    },
    load_sitemap: function (){
        console.log("loading datas?")
        return new Promise( (resolve,reject)=>{
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = function(){
                if(xhr.readyState == 4 && xhr.status == 200){
                    //get JSON datas 
                    var dt = JSON.parse(xhr.responseText);
                    console.log(dt.map);

                    resolve(dt.map);
                } else {
                    console.log(xhr.readyState)
                }
            }
            xhr.open("GET", "my_admin/sitemap",true);
            xhr.send(null);
        });
    },
    addNewEndpoint: function(obj){
        /**
         * envoie une requete au WS, si tout est ok, recupere le nouvel id de l'element
         */
        let content = "parentid="+obj.parentid+"&endpoint="+obj.url;
        return new Promise( (resolve, reject)=>{
            console.log(content)
            var xhr = new XMLHttpRequest();
            //Send the proper header information along with the request
             console.log("header")
            
             console.log("event")
            xhr.onreadystatechange = function(){
                if(xhr.readyState == 4 && xhr.status == 200){
                    //get JSON datas 
                    var dt = JSON.parse(xhr.responseText);
                    
                    console.log("resultat:",dt)
                    resolve(dt);
                } else {
                    console.log("hello",xhr.readyState)
                }
            };
            console.log("open")
            xhr.open("POST", "my_admin/sitemap",true);
            xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
            //les datas...
            console.log("send datas");
            xhr.send(content);
            
        })
    },
    deleteEndpoint: function(id){
        return new Promise( (resolve,reject)=>{
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = function(){
                if(xhr.readyState == 4 && xhr.status == 200){
                    //get JSON datas 
                    var dt = JSON.parse(xhr.responseText);
                    console.log(dt);

                    resolve(dt);
                } else {
                    console.log(xhr.readyState)
                }
            }
            xhr.open("DELETE", "/my_admin/sitemap/"+id,true);
            xhr.send(null);
        });
    }
}


