<template>
    <div class="tree">
    <ul>  
      
        <li>
          
            <div class="endpoint" draggable="true" :id="map.__uuid__">
              
                <h3>Site Index</h3>
                <!-- les methodes supportées -->
                
                <div v-for="(method, name) of http_methods">
                <!-- autres parametres de la vue (query)... -->
                      <span>{{name}}</span>
                      <div v-for="(prop, pn) in simple_props(method)">
                          <span class="param_name">{{pn}}: </span><span>{{prop}}</span>
                      </div>

                      <!-- parametres de requetes -->
                      <div v-if="method.params" class="parameters">
                          <span class="param_name">Parametres:</span>
                          
                              <div v-for="param in method.params" >
                                <span class="param_name">{{param.name}}</span>
                              <span>({{param.type}})</span>
                              </div>
                          
                      </div>
                      <div v-if="method.url_params" class="parameters">
                          <span class="param_name">URL Parametres:</span>
                          
                              <div v-for="qparam in method.url_params">
                                <span class="param_name">{{qparam.name}}</span>
                              <span>({{qparam.type}})</span>
                              </div>
                          
                      </div>
                </div>
            </div>
            <ul v:if="map.childRoutes">
                <map-item 
                v-for="(child, key) in map.childRoutes" 
                :key="child.__uuid__" 
                :item="child" :url="key"></map-item>
                <li>
                  <div class="drag-cible" data-drop="true" v-bind:id="map.__uuid__"><router-link class="round-btn" :to="{path:'/addEndpoint/'+map.__uuid__}">
                  <i class="material-icons">add</i></router-link></div>
              </li>
            </ul>
            
        </li>

    </ul>
    </div>
</template>
<script>

import Sitemap from '../store/sitemap'
import MapItem from './MapItem';


export default {
  name: 'midnight-sitemap',
  data () {
    return {
      title: 'Sitemap Rendering',
      map: {}
    }
  },
  
  mounted: function () { 
    
    Sitemap.load_sitemap().then( (st)=>{
      
      this.$data.map = st;
    })
  },
  components:{
    'map-item':MapItem
  },
  computed:{
      http_methods: function(){
          let obj = {};
          for(let key in this.$data.map){
              if(key!="childRoutes" && !key.startsWith("__")) obj[key]=this.$data.map[key];
          }
          return obj;
      }
  },
  methods:{
      simple_props: function(who){
          let obj = {};
          for(let k in who){
              if(!Array.isArray(who[k])) obj[k]=who[k];
          }
          return obj;
      }
  }
  
}

</script>
<style lang="scss">
@import "./scss/test";

</style>


